import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";

import AppBar from "@material-ui/core/AppBar";
import CssBaseline from "@material-ui/core/CssBaseline";
import Drawer from "@material-ui/core/Drawer";
import Hidden from "@material-ui/core/Hidden";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import Toolbar from "@material-ui/core/Toolbar";
import Divider from "@material-ui/core/Divider";
import Typography from "@material-ui/core/Typography";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import { useParams } from "react-router-dom";
import DeveloperCard from "@components/developer-card";
import MenuLeftPane from "@components/menu-left-pane";
import MenuRightPane from "@components/menu-right-pane";
import { useSelector, useDispatch } from "react-redux";
import ACTIONS from "@redux/actions";
import Snippets from "@components/snippets";
import CreateSnippet from "@components/create-snippet";
import DevelopersGrid from "@components/developers-grid";
import AlertMessage from "@components/alert-message";
import NavWrapper from "@components/nav-wrapper";
import { Box } from "@material-ui/core";

const leftDrawerWidth = 300;
const rightDrawerWidth = 200;

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  leftDrawer: {
    [theme.breakpoints.up("sm")]: {
      width: leftDrawerWidth,
      flexShrink: 0,
    },
  },
  menuButton: {
    marginRight: theme.spacing(2),
    [theme.breakpoints.up("sm")]: {
      display: "none",
    },
  },
  appName: {
    display: "inline-block",
    flexGrow: 1,
    marginLeft: 10,
  },
  // necessary for content to be below app bar
  toolbar: theme.mixins.toolbar,
  leftDrawerPaper: {
    width: leftDrawerWidth,
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
  rightDrawer: {
    width: rightDrawerWidth,
    flexShrink: 0,
  },
  rightDrawerPaper: {
    width: rightDrawerWidth,
  },
}));

function ResponsiveDrawer(props) {
  const { window, page } = props;
  let { developerId } = useParams();
  const classes = useStyles();
  const theme = useTheme();
  const [leftDrawerOpen, setLeftDrawerOpen] = React.useState(false);
  const [rightDrawerOpen, setRightDrawerOpen] = React.useState(false);

  const dispatch = useDispatch();

  const { userId } = useSelector((state) => {
    return {
      userId: state.profile.user.login,
    };
  });

  const [startLoading, setStartLoading] = useState(false);
  const [showAlert, setShowAlert] = useState(false);
  const [alertInfo, setAlertInfo] = useState({});

  useEffect(() => {
    setStartLoading(false);
    setTimeout(() => {
      setStartLoading(true);
    }, 100);
    return function cleanUp() {
      dispatch({
        type: ACTIONS.UNLOAD_DEVELOPER,
      });
    };
  }, [developerId]);

  function handleAlertCallback(alertInfo) {
    setAlertInfo(alertInfo);
    setShowAlert(true);
  }

  function handleCloseAlert() {
    setShowAlert(false);
  }

  const handleLeftDrawerToggle = () => {
    setLeftDrawerOpen(!leftDrawerOpen);
  };

  const handleRightDrawerToggle = () => {
    setRightDrawerOpen(!rightDrawerOpen);
  };

  const container =
    window !== undefined ? () => window().document.body : undefined;

  function getLeftDrawerContent() {
    return (
      <Box>
        <Box
          style={{
            textAlign: "center",
            padding: 10,
            borderBottom: "1px solid lightgray",
          }}
        >
          <Link
            to={"/developer/" + userId}
            style={{
              textDecoration: "none",
              color: "black",
              textAlign: "center",
            }}
          >
            <img
              alt="MSK Snippets Icon"
              style={{
                height: 50,
                width: 50,
                marginBottom: -10,
                marginLeft: -15,
              }}
              src="/images/favicon.png"
            ></img>
            <Typography variant="h4" className={classes.appName}>
              Coders
            </Typography>
          </Link>
        </Box>

        <DeveloperCard developerId={developerId} />
        <br />
        <MenuLeftPane page={page} developerId={developerId} />
      </Box>
    );
  }

  return (
    <div className={classes.root}>
      <CssBaseline />

      {/* <AppBar position="fixed" className={classes.appBar}>
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleLeftDrawerToggle}
            className={classes.menuButton}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" noWrap>
            {page}
          </Typography>
          <div style={{ flex: 1 }}></div>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleRightDrawerToggle}
            className={classes.menuButton}
          >
            <MenuIcon />
          </IconButton>
        </Toolbar>
      </AppBar> */}
      <nav className={classes.leftDrawer}>
        <Hidden smUp implementation="css">
          <Drawer
            container={container}
            variant="temporary"
            anchor="left"
            open={leftDrawerOpen}
            onClose={handleLeftDrawerToggle}
            classes={{
              paper: classes.leftDrawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
          >
            {getLeftDrawerContent()}
          </Drawer>
        </Hidden>
        <Hidden xsDown implementation="css">
          <Drawer
            classes={{
              paper: classes.leftDrawerPaper,
            }}
            variant="permanent"
            open
          >
            {getLeftDrawerContent()}
          </Drawer>
        </Hidden>
      </nav>
      <main className={classes.content}>
        <NavWrapper
          handleLeftDrawerToggle={handleLeftDrawerToggle}
          page={page}
        />
        <div className={classes.toolbar} />
        {startLoading && (
          <Box>
            {(page === "Snippets" || page === "Feed") &&
              userId === developerId && (
                <CreateSnippet alertCallback={handleAlertCallback} />
              )}

            {(page === "Snippets" || page === "Feed") && (
              <Snippets
                isUser={userId === developerId}
                developerId={developerId}
                feed={page === "Feed"}
              />
            )}

            {(page === "Followers" || page === "Following") && (
              <DevelopersGrid developerId={developerId} type={page} />
            )}
          </Box>
        )}
      </main>
      {showAlert && (
        <AlertMessage handleCloseAlert={handleCloseAlert} {...alertInfo} />
      )}

      <Hidden smUp implementation="css">
        <Drawer
          container={container}
          variant="temporary"
          anchor="right"
          open={rightDrawerOpen}
          onClose={handleRightDrawerToggle}
          classes={{
            paper: classes.rightDrawerPaper,
          }}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
        >
          <MenuRightPane />
        </Drawer>
      </Hidden>
      <Hidden xsDown implementation="css">
        <Drawer
          className={classes.rightDrawer}
          variant="permanent"
          open={rightDrawerOpen}
          onClose={handleRightDrawerToggle}
          classes={{
            paper: classes.rightDrawerPaper,
          }}
          anchor="right"
        >
          <MenuRightPane />
        </Drawer>
      </Hidden>
    </div>
  );
}
export default ResponsiveDrawer;
