import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { LANGUAGES } from "@constants/languages";
import { UnControlled as CodeMirror } from "react-codemirror2";

import Button from "@material-ui/core/Button";
import Box from "@material-ui/core/Box";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import { TextField, Typography } from "@material-ui/core";
import Slide from "@material-ui/core/Slide";
import IconButton from "@material-ui/core/IconButton";
import CloseIcon from "@material-ui/icons/Close";
import Grid from "@material-ui/core/Grid";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const useStyles = makeStyles((theme) => {
  return {
    root: {},
    dialogRoot: {},
    dialogTitle: {
      color: "white",
      paddingTop: 5,
      paddingBottom: 5,
    },
    formRoot: {},
    dialogActionsRoot: {
      padding: 20,
    },
    codeMirror: {
      height: "75.4vh",
      border: "1px solid gray",
      marginTop: 10,
      fontSize: 14,
    },
  };
});

export default function SnippetDialog({
  isUser = false,
  snippetId = null,
  open,
  setOpen,
  cursor,
  setCursor,
  language,
  setLanguage,
  title,
  setTitle,
  description,
  setDescription,
  content,
  setContent,
  handleCreateSnippet,
  handleUpdateSnippet,
}) {
  const classes = useStyles();

  const handleClose = () => {
    setOpen(false);
    if (!snippetId) {
      setTitle("My snippet");
      setDescription("");
      setContent("");
    }
  };

  function handleLanguageChange(e) {
    setLanguage(e.target.value);
  }

  return (
    <div className={classes.root}>
      <Dialog
        fullScreen
        className={classes.dialogRoot}
        open={open}
        onClose={handleClose}
        TransitionComponent={Transition}
      >
        <DialogTitle
          className={classes.dialogTitle}
          style={{
            backgroundColor: LANGUAGES[language].color.primary,
          }}
        >
          <IconButton
            edge="start"
            color="inherit"
            onClick={handleClose}
            aria-label="close"
          >
            <CloseIcon />
          </IconButton>
          {snippetId ? title : `Create a ${language} snippet`}
        </DialogTitle>
        <DialogContent>
          <Grid container className={classes.root} spacing={2}>
            <Grid item xs={12} md={3}>
              <Box>
                <br />
                <FormControl
                  style={{
                    minWidth: 100,
                  }}
                >
                  <InputLabel>Language</InputLabel>
                  <Select
                    disabled={snippetId}
                    value={language}
                    onChange={handleLanguageChange}
                  >
                    {Object.keys(LANGUAGES).map((language) => {
                      return (
                        <MenuItem key={language} value={language}>
                          {language}
                        </MenuItem>
                      );
                    })}
                  </Select>
                </FormControl>
                <br />
                <br />
                <br />
                <FormControl
                  style={{
                    width: "100%",
                  }}
                >
                  <TextField
                    disabled={snippetId}
                    required
                    variant="outlined"
                    label="Title"
                    defaultValue={title}
                    onChange={(e) => {
                      setTitle(e.target.value);
                    }}
                  />
                </FormControl>
              </Box>
              <br />
              <br />
              <Box>
                <FormControl
                  style={{
                    width: "100%",
                  }}
                >
                  <TextField
                    rows={5}
                    multiline
                    variant="outlined"
                    disabled={!isUser}
                    label="Description"
                    defaultValue={description}
                    onChange={(e) => {
                      setDescription(e.target.value);
                    }}
                  />
                </FormControl>
              </Box>
            </Grid>
            <Grid item xs={12} md={9}>
              <br />
              <InputLabel>Code snippet</InputLabel>
              <Box className={classes.codeMirror}>
                <CodeMirror
                  cursor={cursor}
                  defaultValue={content}
                  value={content}
                  autofocus={true}
                  onChange={(editor, metadata, value) => {
                    setCursor(editor.doc.getCursor());
                    setContent(value);
                  }}
                  options={{
                    lineWrapping: true,
                    mode: LANGUAGES[language].mode,
                    lineNumbers: true,
                  }}
                />
              </Box>
            </Grid>
          </Grid>

          <DialogContentText id="alert-dialog-description">
            <br />

            <br />
          </DialogContentText>
        </DialogContent>
        <DialogActions className={classes.dialogActionsRoot}>
          <Button variant="outlined" onClick={handleClose}>
            <Typography>Close</Typography>
          </Button>
          <div style={{ flex: 1 }}></div>
          {snippetId && isUser && (
            <Button
              variant="contained"
              style={{
                backgroundColor: LANGUAGES[language].color.primary,
                color: "white",
              }}
              onClick={() => {
                handleUpdateSnippet({
                  snippetId,
                  language,
                  title,
                  description,
                  content,
                });
              }}
            >
              <Typography>Update</Typography>
            </Button>
          )}

          {!snippetId && isUser && (
            <Button
              variant="contained"
              style={{
                backgroundColor: LANGUAGES[language].color.primary,
                color: "white",
              }}
              onClick={handleCreateSnippet}
            >
              <Typography>Create</Typography>
            </Button>
          )}
        </DialogActions>
      </Dialog>
    </div>
  );
}
