import React from "react";
import { Link } from "react-router-dom";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import { useSelector } from "react-redux";

import { AppBar, Toolbar, Typography, IconButton } from "@material-ui/core";
import CssBaseline from "@material-ui/core/CssBaseline";
import Avatar from "@material-ui/core/Avatar";
import ReceiptIcon from "@material-ui/icons/Receipt";
import DashboardIcon from "@material-ui/icons/Dashboard";
import MenuIcon from "@material-ui/icons/Menu";
import Tooltip from "@material-ui/core/Tooltip";

const HtmlTooltip = withStyles((theme) => ({
  tooltip: {
    backgroundColor: "#f5f5f9",
    color: "rgba(0, 0, 0, 0.87)",
    maxWidth: 220,
    fontSize: theme.typography.pxToRem(12),
    border: "1px solid #dadde9",
  },
}))(Tooltip);

const leftDrawerWidth = 300;
const rightDrawerWidth = 200;

const useStyles = makeStyles((theme) => {
  return {
    root: {
      flexGrow: 1,
    },
    title: {
      flexGrow: 1,
      marginLeft: 10,
    },
    appBar: {
      [theme.breakpoints.up("sm")]: {
        width: `calc(100% - ${leftDrawerWidth + rightDrawerWidth}px)`,
        marginLeft: leftDrawerWidth,
        marginRight: rightDrawerWidth,
      },
    },
    navIcon: {
      backgroundColor: theme.palette.grey[300],
      marginRight: 10,
      height: 40,
      width: 40,
    },
    menuButton: {
      marginRight: theme.spacing(2),
      [theme.breakpoints.up("sm")]: {
        display: "none",
      },
    },
  };
});

export default ({ page, handleLeftDrawerToggle }) => {
  const classes = useStyles();
  const { user } = useSelector((state) => {
    return {
      user: state.profile.user,
    };
  });

  return (
    <div className={classes.root}>
      <CssBaseline />

      <div className={classes.content}>
        <AppBar className={classes.appBar} color="default" position="fixed">
          <Toolbar
            style={{ paddingLeft: 10, paddingRight: 10 }}
            position="static"
            variant="dense"
          >
            <IconButton
              color="inherit"
              aria-label="open drawer"
              edge="start"
              onClick={handleLeftDrawerToggle}
              className={classes.menuButton}
            >
              <MenuIcon />
            </IconButton>
            {/* <Link
              to={"/developer/" + user.login}
              style={{
                textDecoration: "none",
                color: "black",
              }}
            >
              <img
                alt="MSK Snippets Icon"
                style={{ height: 40, width: 40 }}
                src="/images/favicon.png"
              ></img>
            </Link> */}
            <Typography variant="h5" className={classes.title}>
              {page}
            </Typography>
            <Link
              to={"/developer/" + user.login}
              style={{
                textDecoration: "none",
                color: "black",
              }}
            >
              <HtmlTooltip
                placement="bottom"
                title={
                  <React.Fragment>
                    <Typography>Feed</Typography>
                  </React.Fragment>
                }
              >
                <IconButton
                  style={{
                    color: page === "Feed" && "rgb(0.9%, 34.7%, 81.6%)",
                  }}
                  className={classes.navIcon}
                >
                  <DashboardIcon />
                </IconButton>
              </HtmlTooltip>
            </Link>
            <Link
              to={"/developer/" + user.login + "/snippets"}
              style={{
                textDecoration: "none",
                color: "black",
              }}
            >
              <HtmlTooltip
                placement="bottom"
                title={
                  <React.Fragment>
                    <Typography>Snippets</Typography>
                  </React.Fragment>
                }
              >
                <IconButton
                  style={{
                    color: page === "snippets" && "rgb(0.9%, 34.7%, 81.6%)",
                  }}
                  className={classes.navIcon}
                >
                  <ReceiptIcon />
                </IconButton>
              </HtmlTooltip>
            </Link>
            <Link to={"/developer/" + user.login + "/Snippets"}>
              <Avatar alt={user.name} src={user.avatar_url} />
            </Link>
          </Toolbar>
        </AppBar>
        {/* <div style={{ display: "flex", flexDirection: "column", flex: 1 }}>
          <Component {...props} />
        </div> */}
      </div>
    </div>
  );
};
